<template>
<div class="gloable-nav">
	<nav class="navbar navbar-default" role="banner">
        <div class="container">
            <div class="navbar-header page-scroll">
                <button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1">
                    <span class="sr-only">Toggle navigation</span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                </button>
                <a class="navbar-brand page-scroll" v-show="isIndex">V2EX</a>
                <span class="glyphicon glyphicon glyphicon-arrow-left back" @click="Back()" v-show="!isIndex"></span>
            </div>
            <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul class="nav navbar-nav navbar-right">
                    <li class="active"><a  v-link="'list'" @click="toggleLatest"></a></li>
                    <li><a  v-link="'list'" @click="toggleHot"></a></li>
                    <li><a  v-link="'node'" @click="toggleNode"></a></li>
                    <li><a></a></li>
                </ul>
            </div>
        </div>
    </nav>
</div>
</template>
<script>
	import router from 'vue-router'
	export default {
		name:'nav',

		methods:{
			toggleCollapse(){
				$("#bs-example-navbar-collapse-1").collapse('hide')
			},
			toggleLatest(){
				this.toggleCollapse()
				// this.$parent.getLatest()
			},
			toggleHot(){
				this.toggleCollapse()
				// this.$parent.getHot()
			},
			toggleNode(){
				this.toggleCollapse()
			},
			Back(){
				history.back(-1)
			}
		},

		computed:{
			isIndex() {
				return this.$route.name === 'list'?true:false
			}
		}
	}
</script>
<style>
	.gloable-nav{
		width: 100%;
		height:auto;
		top:0;
	}
	#bs-example-navbar-collapse-1>ul>li>a{
		text-align: center;
		font-size: 18px;
	}
	.back{
		line-height: 50px;
		margin-left: 10px;
	}
</style>



