/**
 *   Date Date String
 *   (M)(d)(h)(m)(s)(q)  1-2 
 *   (y) 1-4 (S) 1 ( 1-3 )
 *   
 *   (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
 *   (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
 */
const fmtDate = function(date, fmt) {
    var o = {
        "M+": date.getMonth() + 1, //
        "d+": date.getDate(), //
        "h+": date.getHours(), //
        "m+": date.getMinutes(), //
        "s+": date.getSeconds(), //
        "q+": Math.floor((date.getMonth() + 3) / 3), //
        "S": date.getMilliseconds() //
    };
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt))
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

const MillisecondToDate = function(msd) {
    var time = parseFloat(msd) / 1000;
    var str ="";
    if (null != time && "" != time) {

        if (time > 60 && time < 3600) {
            str = parseInt(time / 60.0) + " ";
        }
        else if (time >= 3600 && time < 86400) {
            str = parseInt(time / 3600.0) + " " ;
        }
        else if (time >= 86400 && time < 86400*30) {
            str = parseInt(time / 86400.0) + " " ;
        }
        else if (time >= 86400*30 && time < 86400*365) {
            str = parseInt(time / (86400.0*30)) + " " ;
        }
        else if(time >= 86400*365){
            str = parseInt(time / (86400.0*365)) + " " ;
        }
        else {
            str = parseInt(time) + " ";
        }
    }
    return str;
}

exports.fmtDate = fmtDate
exports.MillisecondToDate = MillisecondToDate



